.developed {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #283645;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
}
@media (min-width: $desktop-version) {
  .developed {
    font-size: 18px;
  }
}
.developed__image {
  width: 27px;
  height: 34px;
  fill: #d22856;
  margin-left: 15px;
}
