.photos-users {
  color: #283645;
  font-size: 14px;
  font-weight: 400;
  line-height: 24px;
}
.photos-users__custom-data {
  background-color: #f2f2f2;
  box-sizing: border-box;
  width: 280px;
  padding-top: 16px;
  padding-left: 17px;
}
@media (min-width: $tablet-version) {
  .photos-users__custom-data {
    width: 100%;
    padding-top: 21px;
    padding-left: 27px;
  }
}
.photos-users__signature {
  width: 250px;
  text-align: center;
  line-height: 30px;
}
@media (min-width: $tablet-version) {
  .photos-users__signature {
    width: 436px;
    font-size: 18px;
  }
}
@media (min-width: $desktop-version) {
  .photos-users__signature {
    width: 570px;
  }
}
.photos-users__user-name {
  font-weight: 700;
  margin: 0;
}
.photos-users__user-comment {
  margin-left: 0;
  margin-right: 0;
  margin-top: 0;
  margin-bottom: 10px;
}
.photos-users__time-of-publication {
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 300;
}
.photos-users__likes {
  color: #d22856;
  font-size: 12px;
  font-weight: 400;
  line-height: 24px;
  text-transform: uppercase;
  margin-top: 0;
  margin-bottom: 0;
  padding-bottom: 20px;
}
@media (min-width: $tablet-version) {
  .photos-users__likes {
    line-height: 29px;
  }
}
.photos-users__image {
  width: 100%;
}
.photos-users__picture {
  line-height: 0;
}
.photos-users__image--panorama {
  width: 320;
  height: 137;
}
@media (min-width: $tablet-version) {
  .photos-users__image--panorama {
    width: 700px;
    height: 509px;
  }
}
@media (min-width: $desktop-version) {
  .photos-users__image--panorama {
    width: 100%;
    height: auto;
  }
}
