.menu {
  display: flex;
  width: 100%;
  position: relative;
  z-index: 1;
  justify-content: center;
  background-color: #283645;
}
@media (min-width: $desktop-version) {
  .menu {
    min-height: 110px;
    background-color: rgba(0, 0, 0, 0.3);
  }
}
.menu__head {
  border-bottom: 1px solid transparent;
  box-shadow: 0 1px 0 #000000, inset 0 1px 0 rgba(255, 255, 255, 0.15);
}
@media (min-width: $desktop-version) {
  .menu__head {
    box-shadow: none;
  }
}
.menu__logo {
  width: 75px;
  height: 22px;
  margin-left: 20px;
  background: url(../img/logo-pink-white-mobile.svg) no-repeat;
  @include link-in-menu;
}
@media (min-width: $tablet-version){
  .menu__logo {
    width: 147px;
    height: 40px;
    margin-left: 0;
    padding-bottom: 20px;
    background: url(../img/logo-pink-white-tablet.svg) no-repeat;
  }
}
@media (min-width: $desktop-version) {
  .menu__logo {
    margin-left: 0;
    background: url(../img/logo-pink-white-desktop.svg) no-repeat;
  }
}
.menu__menu-toggle {
  width: 24px;
  height: 23px;
  margin-right: 20px;
  background-image: url(../img/icon-menu-cross.svg);
  background-repeat: no-repeat;
  background-position: center center;
}
@media (min-width: $tablet-version){
  .menu__menu-toggle {
    margin-right: 0;
  }
}
@media (min-width: $desktop-version) {
  .menu__menu-toggle {
    display: none;
  }
}
.menu__item {
  font-size: 18px;
  text-transform: uppercase;
  font-weight: bold;
  line-height: 32px;
  border-bottom: 1px solid transparent;
  box-shadow: 0 1px 0 #000000, inset 0 1px 0 rgba(255, 255, 255, 0.15);
}
@media (min-width: $desktop-version) {
  .menu__item {
    font-weight: 400;
    text-transform: capitalize;
    line-height: 18px;
    box-shadow: none;
  }
}
.menu__list {
  @include list-reset;
}
.menu__link {
  @include link-in-menu;
}
.menu__script-on {
  display: none;
  &--menu {
    background-color: rgba(0, 0, 0, 0.3);
  }
  &--menu-head {
    box-shadow: none;
  }
  &--menu-toggle {
    background-image: url(../img/icon-menu-burger.svg);
  }
}
@media (min-width: $desktop-version) {
  .menu__script-on {
    display: flex;
  }
}
